.title-1 {
    font-size: 4.23rem;
    font-weight: 700;
    color: var(--color-5);
    strong {
        &:nth-of-type(2n) {
            color: var(--color-3);
        }
        &:nth-of-type(2n+1) {
            color: var(--color-1);
        }
    }
    @media #{$breakpoint-md-down} {
        font-size: 4.03rem;
    }
}

.title-2 {
    font-size: 3.2rem;
    font-weight: 700;
    color: var(--color-5);
}

.title-3 {
    font-size: 2.35rem;
    color: var(--color-1);
    line-height: 2.7rem;
    padding-right: 5rem;
}

.title-4 {
    font-size: 2.35rem;
    color: var(--color-3);
    line-height: 2.7rem;
    padding-bottom: 2rem;
}