#footer {
    z-index: 25;
    background-color: var(--color-1);
    font-size: 0.972rem;
    color: var(--color-9);
    display: flex;
    justify-content: right;
    align-items: center;
    overflow: hidden;
    @media #{$breakpoint-md-up} {
        height: 6.2rem;
    }
    .logo {
        width: 23rem;
        padding-left: 4rem;
        svg {
            width: 11.250rem;
            height: auto;
        }
    }
    .contact {
        width: 5.3rem;
        background: var(--color-2);
        height: 5.3rem;
        border-radius: 18rem;
        color: #000;
        font-size: 1rem;
        position: fixed;
        right: 1.1rem;
        bottom: 0.45rem;
        transition: 0.6s;
        z-index: 25;
        display: flex;
        align-items: center;
        justify-content: center;
        &:hover{
            width: 6.8rem;
            height: 6.5rem;
            border-radius: 3.5rem 0 3.5rem 3.5rem;
            bottom: 2rem;
            font-size: 1.2rem;
        }
    }
    ul{
        width: 70rem;
        display: flex;
        justify-content: right;
        margin-right: 8.5rem;
    }
    li{
        margin: 0 1.5rem;
        a:hover {
            color: var(--color-7);
        }
    }
}

@media #{$breakpoint-md-down} {
    #footer {
        padding-top: 3rem;
        flex-direction: column;
        font-size: 1.5rem;
        text-align: center;
        .logo {
            width: 100%;
            padding-left: 0;
            svg {
                width: 16.5rem;
                height: auto;
                display: inline;
            }
        }
        ul {
            flex-direction: column;
            width: 100%;
            margin: 2.5rem 0 1.5rem 0;
            li {
                margin: 1.5rem 0px
            }
        }
        .contact {
            width: 6.8rem;
            background: var(--color-2);
            height: 6.8rem;
            border-radius: 18rem;
            color: #000;
            font-size: 1.5rem;
            position: fixed;
            right: 1.1rem;
            bottom: 1.15rem;
            transition: 0.6s;
            z-index: 25;
        }
    }
}