/* rich text */
.rte {
    h2 {
        font-size: 3.2rem;
        margin-bottom: 2rem;
        color: var(--color-5);
    }
    h3 {
        font-size: 2.36rem;
        margin-bottom: 1.83rem;
        color: var(--color-1);
    }
    h4 {
        font-size: 1.67rem;
        margin-bottom: 1.7rem;
        color: var(--color-3);
    }
    p {
        font-size: 1.255rem;
        font-weight: 300;
        line-height: 1.79rem;
        margin-bottom: 1.4rem;
        color: var(--color-5);
        a {
            font-weight: 500;
            color: var(--color-1);
            &:before {
                content: url(../img/icons/hyperlink-light.svg);
                width: 2.7rem;
                height: 2.7rem;
                margin-right: 0.3rem;
            }
        }
    }
    ul {
        margin-bottom: 3rem;
        li {
            font-size: 1.255rem;
            font-weight: 300;
            line-height: 1.79rem;
            margin-bottom: 0.85rem;
            color: var(--color-5);
            &:before {
                content: "•";
                margin-right: 2rem;
                font-weight: 700;
                color: var(--color-3);
            }
        }
    }
    ol {
        margin-bottom: 3rem;
        counter-reset: section;
        li {
            font-size: 1.255rem;
            font-weight: 300;
            line-height: 1.79rem;
            margin-bottom: 0.85rem;
            color: var(--color-5);
            position: relative;
            padding-left: 2.5rem;
            &::before {
                counter-increment: section;
                content: counter(section) ".";
                margin-right: 2rem;
                font-weight: 400;
                color: var(--color-3);
                position: absolute;
                left: 0;
            }
        }
    }
    a.btn-1 {
        color: var(--color-1);
        &:before {
            transition: 0.6s;
            content: url(../img/icons/arrow-down.svg) !important;
            transform: rotate(-45deg);
            width: 2.7rem;
            height: 2.7rem;
            padding: 0.63rem;
            border-radius: 2rem;
            background-color: var(--color-2);
            margin-right: 1rem;
        }
        &:hover:before {
            transform: rotate(-90deg);
        }
    }
    &.dark {
        h2,
        h3,
        p,
        ul li,
        ol li,
        a.btn-1 {
            color: var(--color-9);
        }
        h4,
        p a {
            color: var(--color-2);
        }
        p a:before {
            content: url(../img/icons/hyperlink-dark.svg);
        }
    }
    *:nth-last-child(1) {
        margin-bottom: 0;
    }
}