@keyframes animate-logo-header {
    0% {
        transform: translate3d(-10rem, 0, 0);
    }
    100% {
        transform: translate3d(0, 0, 0);
    }
}

#header {
    z-index: 100;
    position: fixed;
    top: 0;
    margin: 1.389rem;
    padding: 0.521rem 0.555rem 0.521rem 0;
    border-radius: 2.292rem;
    box-shadow: 0rem 0.139rem 0.972rem 0rem rgba(0,0,0,0.21);
    background-color: var(--color-9);
    width: calc(100% - 2.778rem);
    transition: height 2s;
    display: flex;
    align-items: center;
    font-size: 1.111rem;
    text-align: center;
    .logo {
        margin-right: auto;
        svg {
            width: auto;
            height: 1.18rem;
            #logo-header-form-4 {
                animation: animate-logo-header 0.4s ease-in-out 0.2s both;
            }
            #logo-header-form-3 {
                animation: animate-logo-header 0.4s ease-in-out 0.4s both;
            }
            #logo-header-form-2 {
                animation: animate-logo-header 0.4s ease-in-out 0.5s both;
            }
            #logo-header-form-1 {
                animation: animate-logo-header 0.4s ease-in-out 0.6s both;
            }
        }
    }
    .navigation-main {
        nav {
            display: flex;
            align-items: center;
            > ul {
                text-align: left;
                display: flex;
                margin-right: 1.389rem;
                > li {
                    display: flex;
                    padding: 0 1.389rem;
                    position: relative;
                    @media #{$breakpoint-lg-down} {
                      padding: 0 0.1rem;
                    }
                    a, svg {
                        position: relative;
                        z-index: 100;
                    }
                    a {
                        cursor: pointer;
                        &:hover{
                            color: var(--color-1);
                        }
                    }
                    svg {
                        display: inline;
                        margin-right: 0.694rem;
                        width: 0.972rem;
                        height: auto;
                    }
                    .icon {
                        margin-left: 0.4rem;
                    }
                    > ul {
                        display: none;
                        top: -1rem;
                        left: 0;
                        right: 0;
                        position: absolute;
                        padding-top: 2.05rem;
                        padding-left: 3.055rem;
                        padding-right: 1.389rem;
                        padding-bottom: 2rem;
                        z-index: 10;
                        li {
                            margin-top: 1.6rem;
                        }
                        &.content-menu {

                        }
                    }
                    svg:first-child {
                        transition: 0.6s;
                        will-change: transform;
                    }
                    &:hover {
                        svg:first-child {
                            transform: translateY(-0.3rem) rotate(180deg);
                        }
                        > ul {
                            border-radius: 1.528rem;
                            background-color: var(--color-8);
                            display: block;
                        }
                    }
                }
            }
        }
        a {
            display: inline-flex;
            align-items: center;
            &.connexion {
                font-weight: 500;
                font-size: 0.903rem;
                border-radius: 1.944rem;
                background-color: var(--color-1);
                color: var(--color-9);
                padding: 0.9rem 1.736rem;
                text-transform: uppercase;
                transition: 0.6s;
                &:hover {
                    background-color: var(--color-4);
                }
                svg {
                    display: inline;
                    margin-right: 0.694rem;
                    width: 0.7rem;
                    height: auto;
                }
            }
        }
    }
    .burger-button {
        @media #{$breakpoint-md-up} {
            display: none;
        }
    }
}

@media #{$breakpoint-md-down} {
    #header .navigation-main nav,
    #header .navigation-main nav > ul,
    #header .navigation-main nav > ul > li {
        display: block;
        text-align: center;
    }
    #header {
        margin: 1rem;
        width: calc(100% - 1.978rem);
        padding: 0.521rem 0.5rem 0.521rem 0;
        .logo svg {
            width: 17rem;
        }
        .navigation-main {
            a {
                &.connexion {
                    position: absolute;
                    bottom: 1rem;
                    left: 1rem;
                    width: calc(100% - 2rem);
                    height: 3.8rem;
                    text-align: center;
                    display: block;
                    font-size: 1.3rem;
                    svg {
                        width: 1rem;
                        height: auto;
                    }
                }
            }
            nav > ul {
                height: 94%;
                width: 100%;
                overflow-y: auto;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                > li {
                    margin: 2.3rem 0;
                    padding: 0;
                    font-size: 2rem;
                    width: calc(100% - 1.5rem);
                    > ul {
                        display: none;
                        position: relative;
                        border-radius: 1.528rem;
                        background-color: var(--color-8);
                        top: -3rem;
                        padding: 3rem 6rem;
                        margin-bottom: -5rem;
                        width: 100%;
                        li {
                            margin-top: 3rem;
                        }
                    }
                    .icon {
                        margin-left: 2rem;
                    }
                    svg {
                        margin-right: 0.35rem;
                        width: 1.45rem;
                    }
                }
            }
        }
    }
    .logo,
    .burger-button{
        position: relative;
        z-index: 500;
    }
    .navigation-main{
        visibility: hidden;
        opacity: 0;
        .navigation-content {
            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            overflow: hidden;
            background: var(--color-4);
            nav{
                background-color: var(--color-9);
                border-radius: 2.1rem;
                position: absolute;
                height: calc(100% - 2rem);
                top: 1rem;
                width: calc(100% - 2rem);
                left: 1rem;
                padding-top: 5rem;
            }
        }
    }
    .close-picto, .burger-picto {
        transition: .6s;
    }
    .close-picto {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        right: 0;
        top: 0;
    }
    .menu-mobile-open{
        .navigation-main{
            transition: .6s;
            visibility: visible;
            opacity: 1;
        }
        .burger-picto {
            visibility: hidden;
            opacity: 0;
        }
        .close-picto {
            visibility: visible;
            opacity: 1;
        }
    }
}