button, a {
    &.btn-1,
    &.btn-2 {
        font-size: 1.1rem;
        margin-right: auto;
        display: flex;
        align-items: center;
        text-transform: uppercase;
        font-weight: 700;
    }
    .picto-btn-1,
    .picto-btn-2 {
        padding: 0.6rem;
        border-radius: 2rem;
        svg {
            transform: rotate(-45deg);
            transition: 0.6s;
            margin-left: 0.05rem;
        }
        &.left {
            margin: 0;
            svg {
                transform: rotate(90deg);
            }
        }
        &.right {
            margin: 0;
            svg {
                transform: rotate(-90deg);
            }
        }

    }
    &:hover {
        .picto-btn-1,
        .picto-btn-2 {
            svg {
                transform: rotate(-90deg);
            }
            &.left {
                svg {
                    transform: rotate(90deg) scale(1.1);
                }
            }
            &.right {
                svg {
                    transform: rotate(-90deg) scale(1.1);
                }
            }
        }
    }
    &.btn-1 {
        color: var(--color-1);
        .picto-btn-1 {
            margin-right: 1rem;
            background-color: var(--color-2);
            svg {
                fill: var(--color-1);
            }
            &.right, &.left {
                margin: 0;
            }
        }
        &:hover {
            color: var(--color-4);
        }
        &.disabled {
          .picto-btn-1 {
            background-color: var(--color-8);
          }
          svg {
            fill: var(--color-9);
          }
        }
    }
    &.btn-2 {
        color: var(--color-9);
        .picto-btn-2 {
            margin-left: 1rem;
            background-color: var(--color-9);
            svg {
                fill: var(--color-1);
            }
        }
        &:hover {
            color: var(--color-9);
        }
    }
}