:root {
    --font-1: "AribauGrotesk", Arial, sans-serif;

    /*
    --font-weight-light: 300;
    --font-weight-regular: 400;
    --font-weight-bold: 700;
    --font-weight-black: 900;
    */

    --ctr-width: 1350px;
    --ctr-margin: 20px;

    --gap: 20px;

    --color-text: #25465f;

    --color-gray-xlight: #f9f9f9;
    --color-gray-light: #eeeeee;
    --color-gray: #b3b3b3;
    --color-gray-dark: #7d7d7d;

    --color-1: #4D2CF8;
    --color-2: #FFF62F;
    --color-3: #92D283;
    --color-4: #241573;
    --color-5: #231F20;
    --color-6: #C8E8C1;
    --color-7: #EEE01D;
    --color-8: #EBEFFF;
    --color-9: #FFFFFF;
    --color-10: #8FB7FB;

    --color-info: #109db9;
    --color-warning: #f6b027;
    --color-success: #009060;
    --color-danger: #d42d02;
}

/* breakpoint mobile first */
$breakpoint-sm-up: "(min-width: 576px)";
$breakpoint-md-up: "(min-width: 769px)";
$breakpoint-lg-up: "(min-width: 992px)";
$breakpoint-xlg-up: "(min-width: 1200px)";
$breakpoint-wd-up: "(min-width: 1650px)";

/* breakpoint desktop first */
$breakpoint-sm-down: "(max-width: 575px)";
$breakpoint-md-down: "(max-width: 768px)";
$breakpoint-lg-down: "(max-width: 991px)";
$breakpoint-xlg-down: "(max-width: 1199px)";
$breakpoint-wd-down: "(max-width: 1649px)";

@media #{$breakpoint-lg-up} {
    :root {
        --ctr-margin: 40px;
        --gap: 40px;
    }
}
